/* ==========================================================================
   Mobile header responsive style - begin
   ========================================================================== */

@include ipad-landscape {

    .mkdf-mobile-header {
        display: block;
    }
}

@include phone-portrait {

    .mkdf-mobile-header .mkdf-mobile-side-area {
        width: 320px;
        @include mkdfTransform(translate3d(320px, 0, 0));

    }

}

/* ==========================================================================
   Mobile header responsive style - end
   ========================================================================== */